@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: relative;
}
/*# sourceMappingURL=App.css.map */
.nav-wrapper #menu,
.sidenav #menu {
  color: #5375e2;
}

.nav-wrapper #right,
.sidenav #right {
  color: #fff;
  background-color: #5375e2;
  font-weight: bold;
}

.nav-wrapper #right:hover,
.sidenav #right:hover {
  background-color: #1b1b1b;
}

.nav-wrapper .logo,
.sidenav .logo {
  display: inline-block;
  height: 100%;
}

.nav-wrapper .logo > img,
.sidenav .logo > img {
  position: absolute;
  top: 50%;
  width: 20%;
  transform: translateY(-50%);
}

.brand-logo {
  margin: 0 10px;
}

.sidenav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/*# sourceMappingURL=Nav.css.map */
.modal #login,
.modal #signup {
  background-color: #5375e2;
  margin: 0 auto;
}
/*# sourceMappingURL=Modal.css.map */
.Loader {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: whitesmoke;
  z-index: 2;
  display: flex;
  justify-content: center;
}
/*# sourceMappingURL=Loader.css.map */
.Header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 10%;
}

.Header div {
  margin: 0 5%;
}

.Header h2 {
  font-family: 'Roboto', sans-serif;
  color: #5375e2;
  width: 100%;
}

.Header img {
  width: 50%;
}

.Header .waves-effect {
  background-color: #5375e2;
  border-radius: 50px;
  border-bottom: 0;
  border-top: 0;
  font-weight: bold;
}

.Header .waves-effect:hover {
  background-color: #1b1b1b;
}
/*# sourceMappingURL=Header.css.map */
.Footer {
  margin-top: 10%;
  bottom: 0;
  width: 100%;
}

.Footer footer {
  background-color: #5375e2;
}

.Footer .grey-text,
.Footer .white-text {
  margin: 0 10px;
}
/*# sourceMappingURL=Footer.css.map */
.Saved {
  margin: 0% 5%;
}

.Saved h3 {
  margin: 0% 4%;
}

.Saved img {
  margin: 5% 0;
  width: 40%;
}
/*# sourceMappingURL=Saved.css.map */
.Home {
  display: flex;
  justify-content: center;
}

.Home img {
  width: 50%;
  margin: 10% auto;
}
/*# sourceMappingURL=Home.css.map */
.ShowResult {
  background-color: whitesmoke;
  margin: 5% 0;
  padding: 7%;
  border-radius: 4px;
}

.ShowResult .error {
  background-color: #be2d2d !important;
  color: #fff !important;
  border-radius: 4px;
  padding: 10px;
}
/*# sourceMappingURL=ShowResult.css.map */
